import React from 'react';
import './index.css';
import App from './App';
import { Route, Redirect } from 'react-router-dom';


const BitcoinVsGold = () => {
  return (
    <Route
      render={({ location }) => {
          return (
            <div>
                <Route exact path="/poll/" render={() => <Redirect to="/" />} />
                <Route
                    location={location}
                             key={location.key}
                             path="/bitcoinvsgold/"
                             component={App}
                />
            </div>
          );
      }}
    />
  );
};

export default BitcoinVsGold;
